.menu {
    position: fixed;
    right: 0;
    top: 50px;
    z-index: 99;
    max-width: 320px;
    height: calc(100vh - 177px);
    display: flex;
    align-items: center;
    justify-content: center;
    background-color: #fff;
    transition: 0.5s;
    transform: translateX(100%);
}
.menu_active {
    transform: translateX(0%);
}
.menu-list {
    display: flex;
    justify-content: space-around;
    align-items: center;
    height: 100%;
    flex-direction: column;
}
.menu-list a {
    text-decoration: none;
    text-transform: uppercase;
    font-weight: 900;
}
.menu-btn {
    width: 35px;
    height: 35px;
    position: absolute;
    right: 320px;
    top: 0;
    z-index: 100;
}

.filter_container_cat {
    height: 200px;
    overflow: hidden;
}

